import * as React from "react"
import * as Constants from "../constants"

const imageStyle = {
    width: "100%", 
    height: "100%",
    objectFit: "contain"
}

export default function getImage(title) {
    var imageSrc = ""
    var background = "#DEE3E8"
    switch(title) {
        case Constants.CP_COMPANY:
            imageSrc = "CPC_landing.png"
            background = "#DEE3E8"
            break;
        case Constants.SWISS_FILMS:
            imageSrc = "SF_landing.gif"
            background = "#000000"
            break;
        case Constants.EXPERIMENTATION:
            imageSrc = "Exp_landing.png"
            background = "#F6F0E7"
            break;
        case Constants.YORK_HOUSE_SCHOOL:
            imageSrc = "YHS_landing.png"
            background = "#F6F0E7"
            break;
        case Constants.AKWSTUDIOS:
            imageSrc = "AKW_landing.gif"
            background = "#DEE3E8"
            break;
        case Constants.PHOTOGRAPHY:
            imageSrc = "PH_landing.png"
            background = "#000000"
            break;
        default:
            imageSrc = "cp_ideation.png"
            break;
    }
    return <img src={imageSrc} 
            style={{...imageStyle, ...{background: background}}} 
            alt=""/>
}